.Bio .p {
    font-size: 20px;
    font-weight: 600;
    margin-top:70px;
    margin-bottom:70px;
}


.Bio-animate-left {
    transition: 1.5s ease-out;
}

.Bio-animate-right {
    transition: 1.5s ease-out;
}

.Bio-static-left {
    transform: translateX(-100%);
}

.Bio-static-right {
    transform: translateX(100%);
}


.Points-div {
    display: inline-block;
}

.Points {
    display: inline-block;
    border-bottom: solid 2px #ff7b00; 
    transition: border-bottom 1.5s;
    transition-delay: 1.5s;
    
}

.Points-static {
    border-bottom: solid 2px #242424; 
}


@media (max-width: 600px) {
    .Bio .p {
        font-size: 15px;
        font-weight: 600;
    }
}