.Contact-form {
    margin-top: 10vh;
}

textarea {
    height: 30px;
    width: 400px;
    color: white;
    margin-left: 10px;
    font-weight: 600;
    font-size: 16px;
    background-color: transparent;
    border: none;
    border-bottom: solid 2px #ff7b00; 
    border-radius: 0px;
    resize: none;
    font-family: sans-serif;
    vertical-align: top;
}

.Textarea {
    font-weight: 600;
}

textarea:focus { 
    outline: none !important;
}

.Name-input {
    width: 400px;
    height: 30px;
    color: white;
    background-color: transparent;
    border: none;
    font-weight: 600;
    font-size: 16px;
    border-bottom: solid 2px #ff7b00;
    border-radius: 0px; 
    margin: 10px 0px 10px 10px;
}

.Email-input {
    width: 400px;
    height: 30px;
    color: white;
    font-size: 16px;
    background-color: transparent;
    border: none;
    font-weight: 600;
    border-bottom: solid 2px #ff7b00;
    border-radius: 0px;
    margin: 10px 0px 10px 10px; 
}

.Field-input {
    font-weight: 600;
    margin-right:150px;
    margin-top: 30px;
}

input:focus { 
    outline: none !important;
}

.Send {
    border-radius: 5px;
    color: white;
    background-color: transparent;
    font-weight: 600;
    position: absolute;
    margin-top: 40px;
    left: 50%;
    margin-left: -75px;
    border: 3px white solid;
    width:150px;
    height:30px;
    background: linear-gradient(to bottom, #ff7b00 50%, #242424 50%);
    background-size: 100% 200%;
    background-position: bottom;
    transition: all .35s ease-out;
}

.Send:hover {
    cursor: pointer;
}

.Send .Share {
    font-size: 18px;
    font-weight: 1000;
    margin-top: 3px;
    display: inline-block;
    position: relative;
    left: 10px;
    top: 3px;
}

.Send:hover {
    background-position: top;
    color: #242424;
    transition: all .35s ease-out;
   }

.Send:hover .Share {
    color: #242424;
    transition: all .5s ease-out;
    transform: rotateX(360deg);
   }    

.Text-align {
    display: inline-block;
    width:100px;
    text-align: right;
}

.Input-align {
    display: inline-block;
    width: 300px;
    text-align: center;
}

.Message-align {
    display: inline-block;
    width: 300px;
    text-align: center;
}

button {
    all: unset;
    width: 150px;
  }

@media (max-width: 600px) {

    .Contact-form {
        font-size: 15px;
    }
    textarea {
        font-size: 15px;
        width: 260px;
    }
    .Name-input{
        font-size: 15px;
        width: 260px;
    }
    .Email-input{
        font-size: 15px;
        width: 260px;
    }
    .Text-align {
        width:60px;
    }
    .Field-input {
        margin-right:10px;
    }
}