.Header-component {
    height: 8vh;
    background-color:#242424;
    text-align: right;
    position: sticky;
    top: 0;
    z-index: 100;
}

.Tool-button {
    color: white;
    background-color: transparent;
    border: none;
    font-weight: bold;
    font-size: 18px;
    margin-top: 2vh;
    margin-right: 25px;
    display: inline-block;
}

.Tool-button:hover {
    cursor:pointer;
   }

.active {
    border-bottom: solid 3px #ff7b00 !important;
    border-radius: 3px;
}

nav {
	background-color: #242424;
	overflow: hidden;
	padding: 1em;
    border-bottom: 1px solid #242424;
}


.navWide {
    display: none;
    margin: 0 auto;
}

.Profile-small {
    height: 30px;
    border-radius: 30px;
    float: left;
    border: solid 2px white;
}

.wideDiv {
    text-align: right;
}

.wideDiv Link {
    text-decoration: none;
    display: inline-block;
    padding: 0 2em;
}

.navNarrow .FaBars {
    float: right;
    cursor: pointer;
    color: rgb(255, 255, 255);
}

.navNarrow .narrowLinks {
    display: none;
}

.navNarrow .narrowLinks li {
        text-decoration: none;
        display: block;
        float: right;
        clear: right;
        padding: 0.5em 0;
        color: white;
        font-weight: bold;
}

.Cv {
    float: left;
    color: white;
    width: 75px;
    height: 25px;
    border: 1px white solid;
    border-radius: 5px;
    text-align: center;
    margin: 3px 0px 0px 15px;
    cursor: pointer;
    font-weight: 600;
    background: linear-gradient(to bottom, #ff7b00 50%, #242424 50%);
    background-size: 100% 200%;
    background-position: bottom;
    transition: all .35s ease-out;
   }
  
   .Cv:hover {
    background-position: top;
    color: #242424;
    transition: all .35s ease-out;
   }
  
   .Cv .Cv-external {
    font-size: 16px;
    font-weight: 800;
    margin-top: 1px;
    display: inline-block;
    position: relative;
    left: 5px;
    top:3px;
   } 
  
   .Cv:hover .Cv-external {
    color: #242424;
    transition: all .5s ease-out;
    transform: rotateX(360deg);
   } 

.navNarrow .narrowLinks li:hover {
    cursor:pointer;
   }


@media (min-width: 600px) {
	
	nav .navWide {
		display: block;
    }

	nav	.navNarrow {
		display: none;
	}

    .Profile-small {
        height: 45px;
        border-radius: 45px;
    }
    .Cv { 
        margin: 9px 0px 0px 30px;
        border: 2px white solid;
       }

}
