.App {
  font-family: sans-serif;
}

body {
  overflow-x: hidden;
}

section {
  height: 100vh;
  padding-top: 8vh;
  padding-bottom: 8vh;
  scroll-snap-align: start;
  background-color:#242424;
  color: white;
  text-align: center;
  position: relative;
}


h1 {
  padding-top: 20px;
}

.Profile-big {
  height: 40vmin;
  border-radius: 40vmin;
  border: solid 4px white;
}

.Intro {
  text-align: center;
  margin: 50px auto auto;
  width: 500px;
}

.Name {
  color: #ff7b00;
  display: inline-block;
}

.Button-test {
  cursor: pointer;
  width:150px;
  height:30px;
  position: absolute;
  bottom: 20vh;
  left: 50%;
  margin-left: -75px;
  border: 3px white solid;
  border-radius: 5px;
  font-weight: 600;
  background: linear-gradient(to bottom, #ff7b00 50%, #242424 50%);
  background-size: 100% 200%;
  background-position: bottom;
  transition: all .35s ease-out;
}

.Button-test:hover {
  background-position: top;
  color: #242424;
  transition: all .35s ease-out;
 }

 .Button-test .Arrow {
  font-size: 18px;
  font-weight: 1000;
  margin-top: 3px;
  display: inline-block;
  position: relative;
  left: 10px;
  top:3px;
 } 

 .Button-test:hover .Arrow {
  color: #242424;
  transition: all .5s ease-out;
  transform: rotateX(360deg);
 } 

.Project-card {
  border: solid 3px white;
  border-radius: 5px;
  height: 470px;
  width: 480px;
}

.Project-card:hover {
  cursor: pointer;
}

.Project-picture img {
  height: 314px;
  width: 474px;
  border: solid 3px white;
  
}

.Project-description {
  position: relative;
  background-color: #242424;
  left: 0px;
  margin-top:-4px !important;
  height: 144px;
  width: 474px;
  border: solid 3px white;
}

a {
  all: unset;
}

.Skill-card {
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: -10px;
  font-size: 14px;
  font-weight: 900;
  text-decoration: underline #ff7b00;
}

h3 {
  margin-top: 15px;
  margin-bottom: 15px;
}

.Project-text {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  font-size: 13.5px;
}

.react-3d-carousel .active {
  border-bottom: none !important;
}

.External {
  font-size: 15px;
  Display: inline-block;
  position: relative;
  left: 10px;
}

.fa-arrow-right {
  color: #ff7b00;
  border-color: white !important;
}

.slider-right div{
  border-color: white !important;
}

.fa-arrow-left {
  color: #ff7b00;
  border-color: white !important;
}

.slider-left div{
  border-color: white !important;
}

.Name-animate {
  font-size: 1em;
  text-align: center;
  font-weight: 600;
  color: #cb3126;
  padding: 0px;
}

.Name-animate span span {
  position: relative;
  animation: move-text-color 0.75s forwards;
  bottom: -1em;
  opacity: 0;
}

@keyframes move-text-color {
  0% {
    bottom: -0.8em;
    opacity: 1;
    color: #3E5C76;
  }
  
  50% {
    bottom: 0.8em;
    color: #ffae00;
  }

  100% {
    bottom: 0;
    opacity: 1;
    color: #ff7b00;
  }
}

.Bio-text {
 width:700px;
 margin-left: auto;
 margin-right: auto;
 margin-top: 15vh;
}

@media (max-width: 750px) {
  .Bio-text {
    width: 500px;
    margin-top: 10vh;
   }
 }

@media (max-width: 600px) {
  .Button-test {
    bottom: 32vh;
  }

  .Project-card {
    height: 340px;
    width: 350px;
    margin-top: 35px;
  }

  .Project-picture img {
    height: 229px;
    width: 346px;
  }

  .Project-description {
    background-color: #242424;
    margin-top:-4px !important;
    height: 100px;
    width: 346px;
  }

  .Skill-card {
    margin-right: 5px;
    margin-left: 5px;
    font-weight: 600;
    font-size: 11.5px;
  }

  h3 {
    margin-top: 8px;
    margin-bottom: 5px;
    font-size: 12px !important;
  }

  .Project-text {
    margin-top: 8px;
    font-size: 11px;
  }

  .External { 
    font-size: 11px;
    left: 5px;
  }

  .Intro {
    font-size: 20px;
    text-align: center;
    margin: 50px auto auto;
    width: 350px;
  }

  .Bio-text {
    width: 350px;
    margin-top: 10vh;
   }

}
